// =========== Custom reset ===========
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box; }
html {
	overflow-x: hidden; }
body {
	box-sizing: border-box;
	font-family: $primary_font;
	color: $primary_color;
	font-weight: 600;
	font-size: 24px;
	background-color: $secondary_color; }
body.lock {
	overflow: hidden; }
summary {
	cursor: pointer; }
h1 {
	// font-family: $secondary_font
	// font-size: 56px
	// font-weight: 400
	// line-height: 56px
 }	// color: $text_color_header
h2 {
	// font-size: 26px
 }	// font-weight: 500
h1, h2, h3 {
	margin: 0;
	padding: 0; }
a {
	color: inherit;
	text-decoration: none; }
li {
	list-style: none; }
.container {
	max-width: 1600px;
	margin: 0 auto; }
img {
	max-width: 100%;
	height: auto; }
button {
	border: none; }
.btn {
	display: block;
	margin: 0 auto;
	max-width: 400px;
	width: 100%;
	height: 70px;
	background-color: $secondary_color;
	color: inherit;
	border-radius: 10px;
	text-align: center;
	line-height: 70px;
	box-shadow: -1px -1px 3px rgba(255, 255, 255, 0.1), 2px 2px 6px rgba(0, 0, 0, 0.8);
	// box-shadow: 0px 0px 10px 5px rgba(255, 255, 255, 0.25)
	transition: all 0.3s ease;
	&:hover {
		box-shadow: inset -1px -1px 3px rgba(255, 255, 255, 0.1), inset 2px 2px 6px rgba(0, 0, 0, 0.8);
		// box-shadow: 0px 0px 10px 5px rgba(51, 51, 51, 0.25)
		// color: $secondary_color
		color: $text_color;
		text-shadow: 0 0 5px $text_color, 0 0 20px $text_color;
 } }		// background-color: $primary_color
